// classmethod 
export function allowExecution<T = unknown>(cb?: (parmater: T) => boolean) {
  return (target: object, key: string, desc: TypedPropertyDescriptor<any>) => {
    const fn = desc.value
    desc.value = function () {
      const params = Array.from(arguments)
      //    @ts-ignore
      return cb?.(...params) && fn(...params)
    }
    return desc
  }
}
